<template>
	<div class="comic">
		<div class="comic__con">
			<button class="comic__close" @click="closeComic">
				<img src="" alt="close" class="comic__close--img" >
			</button>
			<div class="tab">
				<div class="tab__navs">
					<div v-for="(e, t) in tabNav" class="tab__nav" :key="t" :class="{active: tabIndex === t}" @click="(e)=>{return tabChange(t)}"></div>
				</div>
				<div ref="scrollEle" class="tab__contents">
					<ul v-for="(e, t) in tabContent" class="tab__content-list" v-show="tabIndex === t">
						<li v-for="(A, e) in e.imgUrl" class="tab__content-img" :key="e">
							<img alt="comic__img" v-lazy="A" >
						</li>
					</ul>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
export default {
	name: "comic",
	data: function() {
		var A = [];
        return [6, 7, 5, 7].forEach(function (e, n) {
            for (var r = [], a = 0; a < e; a++) r.push(require("./ep0" + (n + 1) + "/page000" + (a + 1) + ".jpg"));
            A.push({
                imgNum: e,
                imgUrl: r
            })
        }), {
            tabIndex: 0,
            tabNav: ["无色", "归宿", "辉光", "风暴"],
            tabContent: A
        }
	},
	methods: {
		init: function() {

		},
		closeComic: function() {
			this.$emit("closeDialog")
		},
		tabChange: function(e) {
			this.tabIndex = e,
			this.$refs.scrollEle.scrollTop = 0
		}
	},
	mounted: function() {
		console.log(this)
		this.init()
	}
}
</script>


