<template>
	<transition enter-active-class="animated faster fadeIn" leave-active-class="animated faster fadeOut">
		<template v-if="isShow">
			<div class="dialog" @click="(e)=>{return e.stopPropagation(), closeDialog(e)}">
				<div class="dialog__con" @click="(e)=>{return e.stopPropagation(), stopEvent(e)}">
					<slot/></slot>
				</div>
			</div>
		</template>
	</transition>

</template>
<script>
export default {
	name: "mihoyoDialog",
	data: function(){
		return {
			isShow: !1
		}
	},
	props: {
		allowHide: {
			type: Boolean,
			default: !0
		}
	},
	methods: {
		hide: function() {
			this.isShow = !1
		},
		show: function() {
			this.isShow = !0
		},
		stopEvent: function() {
			return !1
		},
		closeDialog: function() {
			this.allowHide && this.hide()
		}
	}
	
}
</script>


