<template>
  <div class="home">
    <header class="header">
      <button class="header__button header__button--back" @click="closeWeb" :disabled="flag"></button>
      <button class="header__button header__button--gal" :disabled="flag"></button>
      <button class="header__button header__button--rule" @click="showRule" :disabled="flag"></button>
    </header>
    <aside class="aside-award">
        <div class="final-award">
            <div class="final-award__panel">
                <img :src="require('./images/5.png')" alt="award-img" class="final-award__img">
                <div :data-percent="collectPercent" class="final-award__wave"></div>
                <div v-show="collectPercent < 100" class="final-award__percent">{{collectPercent}}%</div>
                <div :data-state="100 === collectPercent ? 'unlock' : 'lock'" class="final-award__text"></div>
            </div>
        </div>
        <ul class="award">
            <li v-for="award__item in reverseAward" :data-state="award__item.is_get ? 'unlock': 'lock'" class="award__item">
                <img :src="require('./award-img'+award__item.item+'.png')" alt="award__img" class="award__img">
                <img :src="require('./award-text'+award__item.id+'.png')" alt="award__text" class="award__text">
            </li>
        </ul>
        <div class="stamina-num">
            <img :src="require('./p.png')" alt="stamina-num-img" class="stamina-num__img">
            <span class="stamina-num__text">{{staminaNum}}</span>
        </div>
    </aside>
    <div class="main">
        <div class="jigsaw">
            <img src="" alt="ripple" class="ripple">
            <img src="" alt="jigsaw-empty" class="jigsaw__img--template">
            <ul class="jigsaw__pieces">
                <canvas id="canvas" class="needsclick" style="width: 100%;"></canvas>
            </ul>
        </div>
        <div :data-info="pieceInfoIndex" class="jigsaw-info">
            <img src="" alt="jigsaw__avatar" class="jigsaw-info__avatar">
            <div class="jigsaw-info__content"><div class="jigsaw-info__name">{{pieceInfo[pieceInfoIndex].name}}</div>
            <div class="jigsaw-info__percent">{{pieceInfo[pieceInfoIndex].percent}}</div>
            <div class="jigsaw-info__desc">{{pieceInfo[pieceInfoIndex].desc}}</div></div>
            <div :data-index="pieceIndex" class="jigsaw-info__line"></div>
        </div>    </div>
    <aside class="more">
        <button class="more__button more__button--record" @click="showRecord"></button>
        <button class="more__button more__button--comic" @click="showComic"></button>
    </aside>
    <mihoyo-dialog ref="ruleDialog" :allowHide="!0">
        <div class="dialog-rule">
            <img src="" alt="ruleClose" class="dialog-rule__close" @click="closeRule">
            <img src="" alt="rule" class="dialog-rule__img">
        </div>
    </mihoyo-dialog>
    <mihoyo-dialog ref="comicDialog" :allowHide="!1">
        <comic-show @closeDialog="closeComicDialog"/>
    </mihoyo-dialog>
    <mihoyo-dialog ref="recordDialog" :allowHide="!1">
        <div class="dialog-record">
            <img src="" alt="recordClose" class="dialog-record__close" @click="closeRecord">
            <img src="" alt="record" class="dialog-record__img">
            <div class="dialog-record__audio">
                <audio ref="audio" src="" class="audio" v-show="!0"></audio>
                <div class="dialog-record__text" @click="(e)=>{return this.playAudio('cn')}">
                    <img src="" alt="record" class="dialog-record__img" />
                    <button :data-state="this.audioIndex === 0 ? 'on' : 'off'" class="dialog-record__button"></button>
                </div>
                <div class="dialog-record__text" @click="(e)=>{return this.playAudio('jp')}">
                    <img src="" alt="record" class="dialog-record__img" />
                    <button :data-state="this.audioIndex === 1 ? 'on' : 'off'" class="dialog-record__button"></button>
                </div>
            </div>
            <a :data-state="expressionPVlink.length > 0 ? 'unlock' : 'lock'" class="dialog-record__link dialog-record__link--1" @click="()=>{expressionPVlink.length > 0 && openUrl(expressionPVlink)}"></a>
            <a :data-state="rolePVlink.length > 0 ? 'unlock' : 'lock'" class="dialog-record__link dialog-record__link--2" @click="()=>{rolePVlink.length > 0 &&openUrl(rolePVlink)}"></a>
        </div>
    </mihoyo-dialog>
    <mihoyo-dialog ref="awardDialog" :allowHide="!0">
            <div v-show="isAnimStart" class="dialog-award__mask"></div>
            <div class="dialog-award" @click="closeAward">
                <!-- 在实现列表过渡的时候，如果需要过渡的元素，是通过 v-for 循环渲染出来的，不能使用 transition 包裹，需要使用 transitionGroup -->
                <!-- 如果要为 v-for 循环创建的元素设置动画，必须为每一个 元素 设置 :key 属性 -->
                <!-- 给 ransition-group 添加 appear 属性，实现页面刚展示出来时候，入场时候的效果 -->
                <!-- 通过 为 transition-group 元素，设置 tag 属性，指定 transition-group 渲染为指定的元素，如果不指定 tag 属性，默认，渲染为 span 标签 -->
                <transition-group appear tag="ul" appear-active-class="bounce-in" class="dialog-award__list">
                    <li v-for="(e, t) in awardList" :key="e" class="dialog-award__item" :style="'animation-delay:'+ t * .4 + 's'">
                        <div class="dialog-award__light"></div>
                        <img :src="require('./award-img' + e + '.png')" class="dialog-award__img"></li>
                    </li>
                </transition-group>
            </div>
    </mihoyo-dialog>
    <mihoyo-dialog ref="errorDialog" :allowHide="!1">
        <div class="dialog-error">
            <img src="./error.png" alt="" class="dialog-error__img">
            <p class="dialog-error__text">{{errorMsg}}</p>
            <button class="dialog-error__btn">关闭页面</button>
        </div>
    </mihoyo-dialog>
  </div>
</template>

<script>
import api from '../../models/api'
import 'yuki-createjs/lib/easeljs-0.8.2.combined.js'
import comicShow from '@/components/comicShow'
export default{
    name: "home",
    components: {
        comicShow
    },
   data: function() {
    for (var A = [{
        id: 0,
        //avatar: t(590),
        name: "[未知成分]",
        percent: "???",
        desc: "???<br/>???"
    }, {
        id: 1,
        //avatar: t(591),
        name: "[糖分]",
        percent: "30%",
        desc: "甜甜圈可是装在另一个胃里的~"
    }, {
        id: 2,
        //avatar: t(592),
        name: "[家里蹲]",
        percent: "20%",
        desc: "哈~啊~~拯救世界...还不如在家睡觉呢…"
    }, {
        id: 3,
        //avatar: t(593),
        name: "[毒舌]",
        percent: "25%",
        desc: "你的话，在成为伙伴的瞬间我就会果断绝交呢。"
    }, {
        id: 4,
        //avatar: t(594),
        name: "[大触]",
        percent: "15%",
        desc: "想要挑战我？至少先练个100年再来吧。"
    }, {
        id: 5,
        //avatar: t(595),
        name: "[胶佬]",
        percent: "10%",
        desc: "拼胶一时爽，一直拼胶一直爽。"
    }], e = [], n = [], r = [], a = [{
        x: 196,
        y: 142,
        dotX: 311,
        dotY: 245
    }, {
        x: 343,
        y: 382,
        dotX: 393,
        dotY: 395
    }, {
        x: 133,
        y: 301,
        dotX: 236,
        dotY: 348
    }, {
        x: 150,
        y: 460,
        dotX: 282,
        dotY: 462
    }, {
        x: 35,
        y: 424,
        dotX: 103,
        dotY: 505
    }], i = 0; i < 4; i++) e.push({
        id: i + 1,
        item: i + 1,
        is_get: !1
    });

    for (var o = 0; o < 10; o++) n.push(require("./" + o + ".mp3"));
    for (var s = 0; s < 5; s++) r.push({
        id: s + 1,
        isGet: !1,
        bitmap: null,
        getBitmap: null,
        doneBitmap: null,
        lollipopBitmap: null,
        lightBitmap: null,
        // complete: t(607)("./" + (s + 1) + ".png"),
        disable: require("./" + (s + 1) + ".png"),
        // isDone: t(620)("./complete" + (s + 1) + ".png"),
        // lollipop: t(626)("./award-img1" + (s + 1) + ".png"),
        // light: t(627)("./" + (s + 1) + ".png")
    });
    return r.forEach(function (A, e) {
        r[e] = Object.assign(A, a[e])
    }), {
        flag: !1,
        isAnimStart: !1,
        stage: null,
        galCallback: null,
        ruleCallback: null,
        awardCallback: null,
        audioIndex: -1,
        errorMsg: "",
        collectPercent: 0,
        staminaNum: 0,
        expressionPVlink: "",
        rolePVlink: "",
        completedAll: 1,
        completedPart: 0,
        pieceIndex: 1,
        pieceInfoIndex: 0,
        awardList: [],
        pieceList: [],
        pieceInfo: A,
        audioList: n,
        awardInfoList: e,
        pieceConfig: r
    }
   },
   computed: {
    reverseAward: function() {
        return this.awardInfoList.slice(0).reverse()
    }
   },
   mounted: function() {
    this.getHomeInfo()
   },
   methods: {
    getHomeInfo: function() {
        api.getHome().then(e=>{

            const {
                first_in,
                sum,
                collect,
                collect: {
                    get,
                    award,
                    part_info,
                    tips: {
                        percent,
                        completed_all,
                        completed_part
                    }
                },
                show: {
                    link1,
                    link2
                },
              } = e;
              this.awardList = e.collect.get
              this.awardInfoList = award
              this.pieceList = part_info

              this.collectPercent = percent
              this.completedAll = completed_all
              this.completedPart = completed_part,
              this.expressionPVlink = link1
              this.rolePVlink - link2;
              this.staminaNum = sum > 200 ? 200 : n
              this.pieceIndex = this.completedPart || 1
              this.pieceInfoIndex = 0 === this.completedPart && this.pieceList[0].is_get ? 1 : 0
              this.pieceList.forEach((e,t)=>this.pieceConfig[t].isGet = e.is_get);

              //first_in && (this.playGal(), this.galCallback = this.showRule)
              if(this.awardList.length > 0){
                if(first_in) this.ruleCallback = this.showAward;
                else {
                    this.$refs.awardDialog.show(),
                    this.isAnimStart = !0;
                    var timer = setTimeout(() => {
                        this.isAnimStart = !1, clearTimeout(timer)
                    }, 320 * this.awardList.length)
                }
              }
              this.showPieceInfo()
              //this.completedPart ? this.awardCallback = this.showPieceInfo : this.showPieceInfo()
        }).catch((e) => {
            this.errorMsg = "啊，发生了未知错误！"
            this.$refs.errorDialog.show()
            //this.errorMsg = e.data.message || "啊，发生了未知错误！", this.$refs.errorDialog.show()
        })
    },
    showRule: function() {
        this.clearDialog(), this.$refs.ruleDialog.show()
    },
    closeRule: function() {
        this.$refs.ruleDialog.hide()
    },
    closeWeb: function(){
        alert()
    },
    closeComicDialog: function() {
        this.$refs.comicDialog.hide()
    },
    clearDialog: function(){
        /*this.$refs.errorDialog.hide(), this.$refs.galDialog.hide(), this.$refs.awardDialog.hide(), this.$refs.recordDialog.hide()*/
    },
    showRecord: function() {
        this.clearDialog(), this.$refs.recordDialog.show()
    },
    showComic: function() {
        this.clearDialog(), this.$refs.comicDialog.show()
    },
    showPieceInfo: function() {
        this.initJigSaw()
    },
    closeRecord: function() {
        this.clearDialog(), this.$refs.recordDialog.hide()
    },
    playAudio: function(e) {
        let index = -1;
        "cn" === e ? (this.audioIndex = 0, index = Math.floor(5 * Math.random())) : "jp" === e && (this.audioIndex = 1, index = Math.floor(5 * Math.random() + 5)),
        this.$refs.audio.src = this.audioList[index],
        this.$refs.audio.play(),
        this.$refs.audio.onended = () => {
            this.audioIndex = -1
        }
    },
    openUrl: function(link) {
        window.open(link, "_blank");
    },
    closeAward: function() {
        this.clearDialog(), this.$refs.awardDialog.hide()
    },
    initJigSaw: function() {
        this.stage = new createjs.Stage("canvas")
        this.stage.canvas.width = 490
        this.stage.canvas.height = 631
        let map1 = new createjs.Bitmap("");
        this.stage.addChild(map1).set({
            scaleX: 1,
            scaleY: 1
        })
        for (var r = [], a = 0; a < 10; a++) r.push(require("./dot" + a + ".png"));

        var spriteSheet = new createjs.SpriteSheet({
            framerate: 10,
            images: r,
            frames: {
                width: 40,
                height: 40,
            },
            animations: {
                shine: [0, 9, "shine", .5]
            }
        })


        createjs.Ticker.addEventListener("tick", () => {
            this.stage.update()
        })

        if(3 === this.completedAll) {
            this.pieceConfig.forEach((e, t) => {
                this.pieceConfig[t].bitmap = new createjs.Bitmap(e.disable)
                this.stage.addChild(e.bitmap).set({
                    scaleY: 1,
                    scaleY: 1
                })
            })
            map1 = new createjs.Bitmap(require("./jigsaw-full.png"));
            this.stage.addChild(map1).set({
                scaleY: 1,
                scaleX: 1,
                // alpha: .5
            })
            this.pieceConfig.forEach((e,t) => {
                this.pieceConfig[t].doneBitmap = new createjs.Sprite(spriteSheet, "shine")
                this.stage.addChild(this.pieceConfig[t].doneBitmap).set({
                    x: e.dotX,
                    y: e.dotY
                })
            })
            this.initEvent()
            this.initDoneEvent()
        }
        
        
    },
    initEvent: function() {
        this.pieceConfig.forEach((e,t) => {
            e.bitmap.addEventListener('click', () => {
                this.pieceIndex = e.id
                this.pieceInfoIndex = e.isGet ? e.id : 0
            })
        })
    },
    initDoneEvent: function() {
        this.pieceConfig.forEach((e,t) => {
            e.bitmap.addEventListener('click', () => {
                this.pieceIndex = e.id
                this.pieceInfoIndex = e.isGet ? e.id : 0
            })
        })
    }
   }
}
</script>


