import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/main/home/home'
import {beforeRouterUpdate, afterRouterUpdate} from '@/lib/router/index'

Vue.use(Router)


const routes = [{
    path: "/",
    name: "home",
    component: resolve => require(['@/main/home/home'], resolve)
    //component: Home,
    // children: [{
    //     path: "about",
    //     name: "about",
    //     component: r.default
    // }]
}];


const router = new Router({
	routes: routes
})


router.beforeEach((to, from, next) =>{
    beforeRouterUpdate.bind(Vue)(to, from, next)
})
router.afterEach( (to, from, next) =>{
    afterRouterUpdate.bind(Vue)(to, from, next)
})

export default router




