import VueLazyLoad from 'vue-lazyload'
import component from '../lib/component'
import errorImg from '../images/error.png'
import loadingImg from '../images/loading.png'

export default function(A) {
	//A.use(),
	A.use(VueLazyLoad, {
		preLoad: 1.2,
	    error: errorImg,
	    loading: loadingImg,
	    attempt: 1
	}),
	A.use(component)

}


