// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import libRegister from './libRegister'
import filter from './filter'
import  './css/main.scss'

import 'babel-polyfill';
import fastclick from 'fastclick';

import checkSize from '@/utils/checksize'; // 老版checksize的优化版，请勿再手动引入checksize了

Vue.config.productionTip = false

// 根据当前环境选择性加载 mock 包，发布的时候也不用注释和删除
if(process.env.NODE_ENV === 'development') {
  require('./mock');
}


//{-----------------
// FOR H5 处理移动端click 延时 300ms的问题
fastclick.attach(document.body);

checkSize(Object.assign({
	widthParam: 1280,
	heightParam: 720,
	//callback为checksize完成后的回调
	callback: () => {
	// frame 默认是hidden的，checksize完成后，再visible，避免页面checksize前后页面错位抖动
	document.getElementById("frame").style.visibility = "visible";
  }
}));

//miHoYoKeyboardCoverFix.init();
//----------------}

libRegister(Vue)

filter(Vue)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



